@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
:root {
	--red: #9d0a0eff;
	--orange: #c18c5dff;
	--white: #edeff9ff;
	--light-grey: #9da0aaff;
	--dark-grey: #5e6066ff;
	--black: #0b0b0bff;
	--green: #02f2beff;
	--turquoise: #03a0b5ff;
	--blue: #05668dff;
	--fredoka: "Fredoka One", "Open Sans", sans-serif;
}

/* use color names
#element {
  color: var(--color);
} */




html {
	background-color: #9da0aaff;
	background-color: var(--light-grey);
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

input,
select,
textarea {
	border-radius: 20px;
	background: #9da0aaff;
	background: var(--light-grey);
	box-shadow: inset 6px 6px 0px #858891, inset -2px -2px 0px #b5b8c4;
  border: none;
	display:block;
	margin-top: 4px;
}
/* needs a font */

input,
select {
  height: 40px;
  padding-left: 15px;
}

textarea {
  padding:12px;
}




button { 
	border: none;
	border-radius: 50px;
	background: #05668d;
	box-shadow: 6px 6px 0px #045778, -2px -2px 0px #0675a2;
	height: 40px;
	font-weight: bold;
	min-width: 100px;
	margin: 12px;
	padding: 10px;
	font-size: large;
	color: var(--white);
	cursor: pointer;
}

button:focus {
	background: #05669d;
  box-shadow: inset -2px -2px 0px #0675a2, inset 7px 7px 0px #045778;
  outline: 0;
}

select:required:invalid {
  color: var(--light-grey)
}

formInput.converter_select__1flIX {
  max-width: 125px;
  }

option[default] {
  display: none
}

.converter_subtitle__3nEOj {
  color:var(--white);
  font-size: xx-large;
  justify-self: center;
}

.converter_converterContainer__2rawZ {
  display: inline-grid;
  justify-content: center;
  border-radius: 20px;
  color: var(--white);
	background: var(--dark-grey);
	box-shadow: inset 2px 2px 0px #6c6e75, inset -4px -4px 0px #505257;
  border: none;
  padding: 0 30px 30px 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  grid-template-rows: auto auto;
}

.converter_convertForm__1o4BS {
  display: inline-grid;
  grid-template-columns: auto auto;
  grid-row-gap: 20px;
  row-gap: 20px;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  margin-bottom: 10px;
}



.login_loginform__2kWry {
  display: inline-flex;
  flex-direction: row;
}



input {
  margin: 5px;
}
.landingpage_container__Hf7IG {
  display: inline-grid;
  width: 100%;
  grid-template-columns: auto auto -webkit-max-content;
  grid-template-columns: auto auto max-content;
	grid-template-areas:
		"header header nav"
		"main main sidebar"
    "footer footer footer";
  grid-gap: 30px;
  gap: 30px;
}

header {
  grid-area: header;
  margin-left: 20px;

}

nav {
  grid-area: nav;
  background-color: var(--green);

}

main {
  grid-area: main;
  margin-left: 20px;
}

.landingpage_sidebar__3ia96 {
  grid-area: sidebar;


}

footer {
  grid-area: footer;
  margin: 0px;
  color: var(--white);
  background-color: var(--dark-grey);
}



